        label: ''
      }, {
        value: 'Aomen',
        label: ''
      },
        {
          value: 'Chengdu',
          label: ''
        }, {
          value: 'Shenzhen',
          label: ''
        }, {
          value: 'Guangzhou',
          label: ''
        }, {
